.covid-app__loader {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;

    div {
        box-sizing: border-box;
        display: block;
        position: absolute;
        width: 64px;
        height: 64px;
        margin: 8px;
        border: 8px solid lighten(#4D1ED3, 20%);
        border-radius: 50%;
        animation: covid-app__loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
        border-color: lighten(#4D1ED3, 20%) transparent transparent transparent;
    }

    div:nth-child(1) {
        animation-delay: -0.45s;
    }

    div:nth-child(2) {
        animation-delay: -0.3s;
    }

    div:nth-child(3) {
        animation-delay: -0.15s;
    }

}

  @keyframes covid-app__loader {

    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
}
  